import React from "react";
import {
  Grid,
  GridItem,
  Box,
  useRadioGroup,
  useRadio,
  Image,
  Button,
} from "@chakra-ui/react";
import MaleIcon from "./Assets/ConsultSection/Male.svg";
import FemaleIcon from "./Assets/ConsultSection/Female.svg";

const ConsultSection = () => {
  return (
    <>
      <Box p="1rem" border="1px solid">
        <GenderSection />
      </Box>
    </>
  );
};

export default ConsultSection;




const GenderSection = () => {
  return (
    <>
      <Grid
        templateRows="10% 10% 60% 20%"
        placeItems="center"
         gap=".5rem"
      >
        <GridItem color="#8c8c8c" border="1px solid">
          STEP 1/4
        </GridItem>
        <GridItem fontWeight="700" border="1px solid" fontSize="1.5rem">
          Please Select your gender :
        </GridItem>
        <GridItem>
          hello
        </GridItem>
        <GridItem>
          <Box d="flex">
            <Button colorScheme="teal" variant="solid">
              Button
            </Button>
            <Button colorScheme="teal" variant="solid">
              Button
            </Button>
          </Box>
        </GridItem>
      </Grid>
    </>
  );
};
